<template>
  <div class="Me">
    <div class="wallet">
      <img src="@/assets/MyWallet.png"
           alt="">
      <div class="myWallet">
        <div class="name">{{$t('mywallet')}}</div>
        <div class="add">
          <div class="address">{{address}}</div>
          <div class="copy"
               @click="copy(userAddress)">
            <img src="@/assets/copy.png">
          </div>
        </div>
      </div>
    </div>
    <div class="box">
      <div class="my"
           v-for="(el, i) in item"
           :key="i">
        <div class="icon">
          <img :src="el.icon">
        </div>
        <div class="name">{{el.name}}</div>
        <div class="num">{{el.value | NumFormat}}</div>
        <van-button type="info"
                    @click="harvest"
                    :disabled="btnDisabled"
                    :loading="btnLoading"
                    v-if="el.btn"
                    class="btn"
                    plain
                    color="#1F75D9">{{el.btn}}</van-button>
      </div>
    </div>
    <!-- <div class="myNFT">
      <div class="title">My NFT</div>
      <div class="downBox">
        <div class="myDown"
             v-for="(item, index) in list"
             :key="index">
          <div class="myLeft">
            <div class="icon">
              <img :src="item.icon"
                   alt="">
            </div>
            <div class="nums">
              <div class="names">{{item.name}}</div>
              <div class="number">{{item.num}}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="invitation"
         @click="invitation">
      <div class="invitationName">邀请好友</div>
      <img src="@/assets/goRig.png"
           alt="">
    </div> -->
  </div>
</template>

<script>
import {
  Toast
} from 'vant';
import {
  NumFormat
} from '@/util/filter.js'
import BigToSmall from '@/util/BNUtils.js'
export default {
  name: 'heads',
  data () {
    return {
      btnDisabled: false,
      btnLoading: false,
      timer: null,
      list: [
        {
          name: '累计积分',
          icon: require('@/assets/leijijifen.png'),
          num: 50.2654
        },
        {
          name: '今日预估积分',
          icon: require('@/assets/yugujifen.png'),
          num: 50.2654
        }
      ]
    }
  },
  filters: {
    NumFormat
  },
  computed: {
    userAddress () {
      return this.$store.state.address
    },
    address () {
      let formatAddress = this.$util.plusXing(this.$store.state.address || '', 3, 4, '.', 3)
      return formatAddress
    },
    item: {
      get () {
        let lp = this.$store.state.myLp
        let INSum = this.$store.state.getAccountInfoSum['2']
        INSum = (INSum / BigToSmall.pow(1, 18))
        console.log('我的总收益', INSum)
        return [{
          icon: require('@/assets/LP.png'),
          name: this.$t('myLP'),
          value: lp
        },
        {
          icon: require('@/assets/IN.png'),
          name: this.$t('myIncome'),
          value: INSum,
          btn: this.$t('harvest')
        }
        ]
      },
      set (v) {
        return v
      }
    }
  },
  destroyed () {
    clearInterval(this.timer)
  },
  mounted () {
    this.timer = setInterval(() => {
      this.myallLp()
    }, 3000)
  },
  methods: {
    // 分享
    invitation () {
      this.$router.push({
        path: 'invitation'
      })
    },
    // 复制
    copy (val) {
      let domInput = document.createElement('input');
      domInput.value = val;
      document.body.appendChild(domInput);
      domInput.select();
      document.execCommand("Copy");
      Toast(this.$t('copySuccess'))
      domInput.remove()
    },
    // 我质押的总lp
    async myallLp () {
      let poolContractInstance = this.$store.state.poolContractInstance
      let val = this.$store.state.address
      let myTotal = await poolContractInstance.methods.getAccountInfoSum(val).call()
      this.$store.dispatch("ActionsSetGetAccountInfoSum", myTotal);
      let INSum = myTotal['2']
      this.item[1].value = (INSum / BigToSmall.pow(1, 18))
    },
    // 收割
    async harvest () {
      try {
        if (this.item[1].value == 0) return
        let that = this
        this.btnDisabled = true
        this.btnLoading = true
        await this.$store.state.poolContractInstance.methods.harvestAll().send({
          from: that.userAddress
        }).then((res) => {
          Toast(this.$t('success'))
          this.btnDisabled = false
          this.btnLoading = false
          console.log('收割', res)
        })
      } catch (e) {
        console.log('错误', e)
        Toast(this.$t('cancel'))
        this.btnDisabled = false
        this.btnLoading = false
      }


    }
  }
}
</script>

<style scoped lang="less">
img {
  width: 100%;
  height: 100%;
  display: block;
}

.Me {
  background-color: #f6f7fb;
  padding: 10px;
  .invitation {
    margin-top: 5px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px 14px;
    background: #ffffff;
    border-radius: 6px;
    .invitationName {
      font-size: 14px;
      font-weight: 500;
      color: #333333;
    }
    img {
      width: 7px;
      height: 13px;
    }
  }
  .myNFT {
    padding: 28px 15px;
    background: #ffffff;
    border-radius: 6px;
    margin-top: 5px;
    .title {
      font-size: 15px;
      margin-bottom: 25px;
      font-weight: bold;
      color: #333333;
      text-align: center;
    }
    .downBox {
      display: flex;
      .myDown {
        flex: 1;
        .myLeft {
          display: flex;
          align-items: center;
          .nums {
            display: flex;
            flex-direction: column;
            .names {
              font-size: 10px;
              font-weight: 400;
              color: #909cba;
            }
            .number {
              margin-top: 15px;
              font-size: 14px;
              font-weight: 500;
              color: #333333;
            }
          }
          .icon {
            width: 37px;
            height: 37px;
            margin-right: 10px;
          }
        }
      }
    }
  }
  .box {
    display: grid;
    grid-gap: 20px;
    grid-template-columns: 1fr 1fr;
    margin-top: 13px;

    .my {
      background: #fff;
      display: flex;
      border-radius: 6px;
      flex-direction: column;
      padding: 14px;

      .icon {
        height: 32px;
        width: 32px;
      }

      .name {
        font-size: 14px;
        margin-top: 15px;
        margin-bottom: 17px;
      }

      .num {
        font-size: 15px;
        font-weight: bold;
        color: #0076dd;
      }

      .btn {
        margin-top: 25px;
        height: 37px;
        width: 100%;
        line-height: 37px;
        text-align: center;
        // border: 1px solid #1F75D9;
        border-radius: 3px;
        font-size: 14px;
        font-weight: 500;
        // color: #1F75D9;
      }
    }
  }

  .wallet {
    height: 157px;
    width: 100%;
    position: relative;

    .myWallet {
      position: absolute;
      top: 61px;
      left: 18px;

      .name {
        font-size: 14px;
        font-weight: 500;
        color: #ffffff;
      }

      .add {
        margin-top: 18px;
        display: flex;
        align-items: center;

        .address {
          font-size: 17px;
          font-weight: 500;
          color: #ffffff;
          line-height: 1;
        }

        .copy {
          margin-left: 10px;
          height: 13px;
          width: 13px;
        }
      }
    }
  }
}
</style>
